import React, { Component } from "react";
import { Link } from "gatsby";
import config from "../../data/SiteConfig";
import Categories from "./Categories";
import styles from "./Header.module.scss";
import { myContext } from "./Provider";

const Header = () => (
  <myContext.Consumer>
    {context => (
      <header>
        <h1>
          <Link to="/about">About Me</Link>
        </h1>
        <nav className={context.isDark ? "darkTheme" : "lightTheme"}>
          <style>{'body { background-color: red; }'}</style>
          <ul className={styles.mainNav}>
            <li>
              <Link to="/" activeClassName={styles.activeNav}>
                All Posts
              </Link>
            </li>
            <Categories activeClassName={styles.activeNav} />
          </ul>
          <h1>{context.isDark ? "Dark Theme" : "Light Theme"}</h1>
          <button onClick={() => context.changeTheme()}>
            {context.isDark ? "Light" : "Dark"}
          </button>
        </nav>
      </header>
    )}
  </myContext.Consumer>
);

export default Header;
